import React, { Component } from 'react'
import { Icon, Menu } from 'antd'
import { Link, withRouter } from 'react-router-dom'
const { SubMenu } = Menu

class CustomMenu extends Component {
  state = {
    openKeys: [],
    selectedKeys: []
  }
  renderMenuItem = ({key, icon, title}) => {
    return (
      <Menu.Item key={key}>
        <Link to={key}>
          { icon && <Icon type={icon} /> }
          <span>{title}</span>
        </Link>
      </Menu.Item>
    )
  }
  renderSubMenu = ({key, icon, title, subs}) => {
    return (
      <SubMenu
        key={key}
        title={<span>{icon && <Icon type={icon} />}<span>{title}</span></span>}
      >
        {
          subs && subs.map(item => {
            return item.subs && item.subs.length > 0 ? this.renderSubMenu(item) : this.renderMenuItem(item)
          })
        }
      </SubMenu>
    )
  }
  onOpenChange = (openKeys) => {
    //æ­¤å‡½æ•°çš„ä½œç”¨åªå±•å¼€å½“å‰çˆ¶çº§èœå•ï¼ˆçˆ¶çº§èœå•ä¸‹å¯èƒ½è¿˜æœ‰å­èœå•ï¼‰
    if (openKeys.length === 0 || openKeys.length === 1) {
      this.setState({
        openKeys
      })
      return
    }

    //æœ€æ–°å±•å¼€çš„èœå•
    const latestOpenKey = openKeys[openKeys.length - 1]
    //åˆ¤æ–­æœ€æ–°å±•å¼€çš„èœå•æ˜¯ä¸æ˜¯çˆ¶çº§èœå•ï¼Œè‹¥æ˜¯çˆ¶çº§èœå•å°±åªå±•å¼€ä¸€ä¸ªï¼Œä¸æ˜¯çˆ¶çº§èœå•å°±å±•å¼€çˆ¶çº§èœå•å’Œå½“å‰å­èœå•
    //å› ä¸ºæˆ‘çš„å­èœå•çš„keyåŒ…å«äº†çˆ¶çº§èœå•ï¼Œæ‰€ä»¥ä¸ç”¨åƒå®˜ç½‘çš„ä¾‹å­å•ç‹¬å®šä¹‰çˆ¶çº§èœå•æ•°ç»„ï¼Œç„¶åæ¯”è¾ƒå½“å‰èœå•åœ¨ä¸åœ¨çˆ¶çº§èœå•æ•°ç»„é‡Œé¢ã€‚
    //åªé€‚ç”¨äº3çº§èœå•
    if (latestOpenKey.includes(openKeys[0])) {
      this.setState({
        openKeys
      })
    } else {
      this.setState({
        openKeys: [latestOpenKey]
      })
    }
  }
  componentWillReceiveProps(nextProps) {
    //å½“ç‚¹å‡»é¢åŒ…å±‘å¯¼èˆªæ—¶ï¼Œä¾§è¾¹æ è¦åŒæ­¥å“åº”
    const pathname = nextProps.location.pathname
    if (this.props.location.pathname !== pathname) {
      this.setState({
        selectedKeys: [pathname],
      })
    }
  }
  componentDidMount() {
    // è§£å†³ä¾§è¾¹æ åˆ·æ–°åˆå§‹åŒ–çš„é—®é¢˜
    const pathname = this.props.location.pathname
    //è·å–å½“å‰æ‰€åœ¨çš„ç›®å½•å±‚çº§
    const rank = pathname.split('/')
    switch (rank.length) {
      case 2 :  //ä¸€çº§ç›®å½•
        this.setState({
          selectedKeys: [pathname]
        })
        break;
      case 5 : //ä¸‰çº§ç›®å½•ï¼Œè¦å±•å¼€ä¸¤ä¸ªsubMenu
        this.setState({
          selectedKeys: [pathname],
          openKeys: [rank.slice(0, 3).join('/'), rank.slice(0, 4).join('/')]
        })
        break;
      default :
        this.setState({
          selectedKeys: [pathname],
          openKeys: [pathname.substr(0, pathname.lastIndexOf('/'))]
        })
    }
  }
  render() {
    const { menus } = this.props
    const {openKeys, selectedKeys} = this.state
    const { collapsed } = this.props
    const isCollapsed = collapsed ? {} : {openKeys, selectedKeys}
    return (
      <Menu
        mode="inline"
        defaultSelectedKeys={['1']}
        defaultOpenKeys={['sub1']}
        onClick={({key}) => this.setState({selectedKeys: [key]})}
        onOpenChange={this.onOpenChange}
        {...isCollapsed}
        style={{ height: '100%', borderRight: 0 }}
      >
        {
          menus && menus.map(item => {
            return item.subs && item.subs.length > 0 ? this.renderSubMenu(item) : this.renderMenuItem(item)
          })
        }
      </Menu>
    )
  }
}

export default withRouter(CustomMenu)
