import { View } from '@antv/data-set'
export const pieChartData = (data) => {
  const dataDv = new View()
  dataDv.source(data).transform({
    type: 'percent',
    field: 'count',
    dimension: 'item',
    as: 'percent'
  })
  return dataDv
}

